@font-face {
  font-family: NSABold;
  src: url("../fonts/Noto_Sans/NotoSans-Bold.ttf");
}

@font-face {
  font-family: NSARegular;
  src: url("../fonts/Noto_Sans/NotoSans-Regular.ttf");
}

@font-face {
  font-family: NSAMedium;
  src: url("../fonts/Noto_Sans/NotoSans-Medium.ttf");
}

@font-face {
  font-family: NSALight;
  src: url("../fonts/Noto_Sans/NotoSans-Light.ttf");
}

@font-face {
  font-family: NSAThin;
  src: url("../fonts/Noto_Sans/NotoSans-Thin.ttf");
}

body {
  font-family: NSARegular, "Roboto", sans-serif;
}

body::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  overflow: hidden;
}

body::-webkit-scrollbar-track {
  background: white;
}

body::-webkit-scrollbar-thumb {
  background: cadetblue;
}

p {
  line-height: 24px;
}

a {
  text-decoration: none;
}

a:hover {
  color: red;
}

@media screen and (max-width: 768px) {
  nav .container .nav_menu .menuBtn {
    display: flex;
  }

  nav .container .nav_menu ul {
    flex-flow: column;
    justify-content: flex-start;
    position: absolute;
    top: 0px;
    width: 580px;
    overflow: hidden;
    left: -5000px;
    background: white;
    transition: left 0.2s linear;
  }

  nav .container .nav_menu ul li a {
    height: 60px;
    padding: 30px 20px;
  }

  nav .container .nav_menu ul.active_nav {
    left: 0px;
    height: 100vh;
  }
}

@media screen and (max-width: 768px) and (max-width: 680px) {
  nav .container .nav_menu ul {
    width: 500px;
  }
}

@media screen and (max-width: 768px) and (max-width: 580px) {
  nav .container .nav_menu ul {
    width: 450px;
  }
}

@media screen and (max-width: 768px) and (max-width: 520px) {
  nav .container .nav_menu ul {
    width: 400px;
  }
}

@media screen and (max-width: 768px) and (max-width: 480px) {
  nav .container .nav_menu ul {
    width: 350px;
  }
}

@media screen and (max-width: 768px) and (max-width: 420px) {
  nav .container .nav_menu ul {
    width: 300px;
  }
}

@media screen and (max-width: 768px) and (max-width: 380px) {
  nav .container .nav_menu ul {
    width: 280px;
  }
}

@media screen and (max-width: 768px) and (max-width: 350px) {
  nav .container .nav_menu ul {
    width: 250px;
  }
}